<template>
  <div class="container">
    <div class="container-title">Layout 布局</div>
    <h3 class="title">分栏间隔</h3>
    <div class="demo-block">
      <sl-row :gutter="20">
        <sl-col span="4"><div class="demoCol">hi</div></sl-col>
        <sl-col span="4"><div class="demoCol">hi</div></sl-col>
        <sl-col span="4"><div class="demoCol">hi</div></sl-col>
        <sl-col span="4"><div class="demoCol">hi</div></sl-col>
        <sl-col span="4"><div class="demoCol">hi</div></sl-col>
        <sl-col span="4"><div class="demoCol">hi</div></sl-col>
      </sl-row>
    </div>
    
    <h3 class="title">响应式布局</h3>
    <p class="small-title">参照了 Bootstrap 的 响应式设计，预设了五个响应尺寸：xs、sm、md、lg 和 xl。</p>
    <div class="demo-block">
      <sl-row :gutter="10">
        <sl-col :xs="8" :sm="6" :md="4" :lg="3" :xl="1"><div class="demoCol"></div></sl-col>
        <sl-col :xs="4" :sm="6" :md="8" :lg="9" :xl="11"><div class="demoCol"></div></sl-col>
        <sl-col :xs="4" :sm="6" :md="8" :lg="9" :xl="11"><div class="demoCol"></div></sl-col>
        <sl-col :xs="8" :sm="6" :md="4" :lg="3" :xl="1"><div class="demoCol"></div></sl-col>
      </sl-row>
    </div>
    

    <h3 class="title">分栏偏移</h3>
    <p class="small-title">支持偏移指定的栏数</p>
    <div class="demo-block">
      <sl-row :gutter="20">
        <sl-col :span="6"><div class="demoCol"></div></sl-col>
        <sl-col :span="6" :offset="6"><div class="demoCol"></div></sl-col>
      </sl-row>
      <sl-row :gutter="20">
        <sl-col :span="6" :offset="6"><div class="demoCol"></div></sl-col>
        <sl-col :span="6" :offset="6"><div class="demoCol"></div></sl-col>
      </sl-row>
      <sl-row :gutter="20">
        <sl-col :span="12" :offset="6"><div class="demoCol"></div></sl-col>
      </sl-row>
    </div>
    
  </div>
</template>
<script>

export default {
  name: 'Container',
  data(){
    return {}
  },
  components: {
    
  }
}
</script>
<style scoped lang="scss">
  .container {
    .container-title {
      font-size: 24px;
      font-weight: 400;
      color: #1f2f3d;
    }
    .title {
      margin: 55px 0 20px;
      font-size: 20px;
      font-weight: 400;
      color: #1f2f3d;
    }
    .small-title {
      font-size: 14px;
      line-height: 1.5em;
      color: #5e6d82;
    }
    .demoCol {
      min-height: 36px;
      line-height: 36px;
      text-align: center;
      background: #d3dce6;
      border-radius: 4px;
    }
    .demo-block {
      padding: 24px;
      margin-top: 20px;
      background: #fff;
      border: 1px solid #ebebeb;
      border-radius: 3px;
      transition: .2s;
    }
  }

</style>


